"use client"

import { useState, useEffect } from "react"
import { LandingPage } from "@/components/landing-page"
import { LoadingScreen } from "@/components/loading-screen"
import { ResultsDashboard } from "@/components/results-dashboard"
import type { GitHubData } from "@/lib/types"

interface SessionUser {
  username: string
  avatarUrl: string
  name: string | null
}

export default function Home() {
  const [state, setState] = useState<"landing" | "loading" | "results">("landing")
  const [githubData, setGithubData] = useState<GitHubData | null>(null)
  const [vibeMode, setVibeMode] = useState<"hype" | "roast">("hype")
  const [error, setError] = useState<string | null>(null)
  const [sessionUser, setSessionUser] = useState<SessionUser | null>(null)
  const [isCheckingSession, setIsCheckingSession] = useState(true)

  // Check for existing session on mount
  useEffect(() => {
    checkSession()
  }, [])

  const checkSession = async () => {
    try {
      const response = await fetch("/api/auth/session")
      const data = await response.json()

      if (data.authenticated) {
        setSessionUser(data.user)
      }
    } catch (err) {
      console.error("Session check failed:", err)
    } finally {
      setIsCheckingSession(false)
    }
  }

  const handleGenerate = async (username: string, mode: "hype" | "roast", useOAuth: boolean) => {
    setState("loading")
    setVibeMode(mode)
    setError(null)

    try {
      const response = await fetch("/api/github", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username, useOAuth }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || "Failed to fetch GitHub data")
      }

      setGithubData(data)
      setState("results")
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
      setState("landing")
    }
  }

  const handleLogout = async () => {
    try {
      await fetch("/api/auth/logout", { method: "POST" })
      setSessionUser(null)
      setGithubData(null)
      setState("landing")
    } catch (err) {
      console.error("Logout failed:", err)
    }
  }

  const handleReset = () => {
    setState("landing")
    setGithubData(null)
    setError(null)
  }

  if (isCheckingSession) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="animate-pulse text-[#ff00ff]">Loading...</div>
      </div>
    )
  }

  if (state === "loading") {
    return <LoadingScreen />
  }

  if (state === "results" && githubData) {
    return <ResultsDashboard data={githubData} vibeMode={vibeMode} onReset={handleReset} />
  }

  return <LandingPage sessionUser={sessionUser} onGenerate={handleGenerate} onLogout={handleLogout} error={error} />
}
